import React, {Component} from 'react';
import { BrowserRouter } from 'react-router-dom'
import Main from './app/Main'
import Header from './app/shared/Header/Header'
import './App.css';
class Themes extends Component {
	render() {
		return (
			
			<BrowserRouter>
				<div>
					<Header/>
					<Main />
				</div>
			</BrowserRouter>
		);
	}
}

export default Themes;